
.y-checkbox{position:relative
}
.y-checkbox .y-checkbox-box{position:relative
}
.y-checkbox .y-checkbox-box .y-checkbox-dom{transition:.3s all;position:absolute;top:50%;left:0;margin-top:-9px;display:block;border-width:1px;box-shadow:inset 0 0 0 1px rgba(0,0,0,0.2);width:16px;height:16px;border-radius:4px;cursor:pointer;background:#fff
}
.y-checkbox .y-checkbox-box .y-checkbox-dom .check{transition:.3s all;display:block;margin-left:1px;margin-top:1px;text-align:center;font-size:12px;color:#fff;transform:scale(0)
}
.y-checkbox .y-checkbox-box input{position:relative;top:0;left:0;bottom:0;right:0;z-index:-1;opacity:0
}
.y-checkbox.checked .y-checkbox-dom{box-shadow:none;background:#108ee9
}
.y-checkbox.checked .y-checkbox-dom .check{transform:scale(1)
}
.y-checkbox.disabled{cursor:not-allowed
}
.y-checkbox.disabled .y-checkbox-dom{cursor:not-allowed;box-shadow:inset 0 0 0 1px #d9d9d9;background:#cecece;background-color:#f3f3f3
}
.y-checkbox.disabled .y-checkbox-dom path{fill:#cecece
}
.y-checkbox.disabled .check-content{color:#ccc
}


