
.y-input-wrapper{display:inline-block;position:relative
}
.y-input-wrapper .y-input{position:relative;display:inline-block;padding:4px 7px;width:100%;height:28px;cursor:text;font-size:12px;line-height:1.5;color:#666;background-color:#fff;background-image:none;border:1px solid #d9d9d9;border-radius:4px;outline:none;transition:border .2s cubic-bezier(.645, .045, .355, 1),background .2s cubic-bezier(.645, .045, .355, 1),box-shadow .2s cubic-bezier(.645, .045, .355, 1)
}
.y-input-wrapper .y-input.y-input-lg{padding:6px 7px;height:32px
}
.y-input-wrapper .y-input.y-input-sm{padding:1px 7px;height:22px;border-radius:2px
}
.y-input-wrapper .y-input:hover{border-color:#40a5ed
}
.y-input-wrapper .y-input:focus{border-color:#40a5ed;outline:0;box-shadow:0 0 0 2px rgba(64,165,237,0.2)
}
.y-input-wrapper .y-input-group-addon-before,.y-input-wrapper .y-input-group-addon-after{padding:4px 7px;font-size:12px;font-weight:normal;line-height:1;color:#666;text-align:center;background-color:#eee;border:1px solid #d9d9d9;border-radius:4px;position:relative;display:table-cell;width:1px
}
.y-input-wrapper .y-input-group-addon-before{border-top-right-radius:0;border-bottom-right-radius:0
}
.y-input-wrapper .y-input-group-addon-before:first-child{border-right:0
}
.y-input-wrapper .y-input-group-addon-before:first-child{border-right:0
}
.y-input-wrapper .y-input-group-addon-after{border-top-left-radius:0;border-bottom-left-radius:0
}
.y-input-wrapper .y-input-group-addon-after:last-child{border-left:0
}
.y-input-wrapper.y-input-group{position:relative;display:table;border-collapse:separate;width:100%
}
.y-input-wrapper.y-input-group .y-input{display:table-cell
}
.y-input-wrapper.y-input-group .y-input:first-child{border-top-right-radius:0;border-bottom-right-radius:0
}
.y-input-wrapper.y-input-group .y-input:last-child{border-top-left-radius:0;border-bottom-left-radius:0
}
.y-input-wrapper.y-input-group .y-input:not(:first-child):not(:last-child){border-radius:0
}
.y-input-wrapper .y-input-icon{position:absolute;right:6px;top:50%;margin-top:-8px
}
.y-input-wrapper .y-input-icon:hover path{transition:.3s all;fill:#108ee9
}


