
.y-select{width:150px
}
.y-select .y-select-i{position:relative
}
.y-select .y-select-i .ion{cursor:pointer;transition:transform .3s;display:block;width:24px;height:24px;line-height:24px;top:50%;margin-top:-11px;right:0;position:absolute
}
.y-select .y-select-i .ion svg{transition:.2s cubic-bezier(.645, .045, .355, 1) all;margin-top:4px;margin-left:4px;transform:rotateZ(0deg)
}
.y-select .y-select-i .ion svg.x{transition:none
}
.y-select .y-select-i .ion svg.active{transform:rotateZ(-180deg)
}
.y-select .y-select-i .ion:before{display:block;text-align:center;font-size:8px;position:absolute;top:50%;left:50%;margin-top:-8px;margin-left:-9px;width:18px;height:18px;line-height:18px
}
.y-select .y-select-input{transition:.3s all;cursor:pointer;width:100%;outline:none;border:1px solid #d9d9d9;padding:6px 28px 6px 6px;border-radius:4px;font-size:14px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.y-select .y-select-input .default{color:#ccc;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.y-select .y-select-input:hover{border-color:#40a5ed
}
.y-select.disabled{cursor:not-allowed
}
.y-select.disabled .y-select-input:hover{border-color:#d9d9d9
}
.y-select.disabled .y-select-input:disabled{background:#f7f7f7;color:#ccc;cursor:not-allowed
}
.y-select.disabled *{cursor:not-allowed
}
.y-select-dropdown{position:absolute;box-shadow:0 1px 6px rgba(0,0,0,0.2);border-radius:4px;overflow:hidden;background:#fff
}
.y-select-dropdown li.y-select-item{transition:transform .2s cubic-bezier(.645, .045, .355, 1),opacity .2s cubic-bezier(.645, .045, .355, 1),background .2s cubic-bezier(.645, .045, .355, 1);padding:6px;cursor:pointer
}
.y-select-dropdown li.y-select-item.active{background:#f9f9f9 !important;font-weight:700
}
.y-select-dropdown li.y-select-item:hover{background:#e4f8ff
}
.y-select-dropdown li.y-select-item.disabled{color:#ccc;cursor:not-allowed
}
.y-select-dropdown li.y-select-item.disabled:hover{background:none
}


