
.y-tooltip{display:inline-block
}
.y-tooltips{position:absolute;z-index:999999;will-change:opacity;transition:margin .3s cubic-bezier(.08, .82, .17, 1),transform .3s cubic-bezier(.08, .82, .17, 1),opacity .3s cubic-bezier(.08, .82, .17, 1)
}
.y-tooltips.blue .y-tooltips-box{background:rgba(24,151,217,0.95)
}
.y-tooltips.blue .y-tooltips-box *{color:#fff
}
.y-tooltips.blue .y-tooltips-box a{color:#fff;text-decoration:underline
}
.y-tooltips.blue .y-tooltips-delta{border-bottom:4px solid rgba(23,148,212,0.9)
}
.y-tooltips.dark .y-tooltips-box{background:rgba(64,64,64,0.85);color:#fff
}
.y-tooltips.dark .y-tooltips-delta{border-bottom:4px solid rgba(64,64,64,0.85)
}
.y-tooltips.white .y-tooltips-box{background:#fff;color:#666;border:1px solid #e6e6e6;box-shadow:0 0 20px 0 #ccc
}
.y-tooltips.white .y-tooltips-delta{border-bottom:4px solid #e6e6e6
}
.y-tooltips.white .y-tooltips-delta:before{content:"";position:absolute;display:block;width:0;height:0;border-left:4px solid transparent;border-right:4px solid transparent;border-bottom:4px solid #fff;margin-left:-4px;margin-top:1px
}
.y-tooltips .y-tooltips-box{overflow:hidden;padding:6px 8px;border-radius:4px
}
.y-tooltips .y-tooltips-box.slot{padding:6px 12px
}
.y-tooltips .y-tooltips-delta{position:absolute;display:block;width:0;height:0;border-left:4px solid transparent;border-right:4px solid transparent
}
.y-tooltips.bottom{padding-top:4px
}
.y-tooltips.bottom .y-tooltips-delta{margin-left:-4px;top:0;left:50%
}
.y-tooltips.bottom-left{padding-top:4px
}
.y-tooltips.bottom-left .y-tooltips-delta{margin-left:-4px;top:0;left:15px
}
.y-tooltips.bottom-right{padding-top:4px
}
.y-tooltips.bottom-right .y-tooltips-delta{margin-left:-5px;top:0;right:15px
}
.y-tooltips.top{padding-bottom:4px
}
.y-tooltips.top .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;left:50%
}
.y-tooltips.top-left{padding-bottom:4px
}
.y-tooltips.top-left .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;left:15px
}
.y-tooltips.top-right{padding-bottom:4px
}
.y-tooltips.top-right .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;right:15px
}
.y-tooltips.left{padding-left:4px
}
.y-tooltips.left .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;top:50%;left:-1px
}
.y-tooltips.left-top{padding-left:4px
}
.y-tooltips.left-top .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;top:10px;left:-2px
}
.y-tooltips.left-bottom{padding-left:4px
}
.y-tooltips.left-bottom .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;bottom:10px;left:-2px
}
.y-tooltips.right{padding-right:4px
}
.y-tooltips.right .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;top:50%;right:-1px
}
.y-tooltips.right-top{padding-right:4px
}
.y-tooltips.right-top .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;top:10px;right:-2px
}
.y-tooltips.right-bottom{padding-right:4px
}
.y-tooltips.right-bottom .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;bottom:10px;right:-2px
}
.tooltips-enter,.tooltips-leave-active{opacity:0;transform:scale3d(.8, .8, 1) !important
}


