
.y-pagination{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.y-pagination li{cursor:pointer;display:inline-block;width:28px;height:28px;line-height:28px;text-align:center;border-style:solid;border-width:1px;border-color:#d9d9d9;margin-right:6px;border-radius:4px;font-size:14px;transition:all .3s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.y-pagination li:hover{border-color:#108ee9;color:#108ee9
}
.y-pagination li:hover svg path{fill:#108ee9
}
.y-pagination li.disabled{cursor:not-allowed;border-color:#e1e1e1;color:#e1e1e1
}
.y-pagination li.disabled svg path{fill:#e1e1e1
}
.y-pagination li.prev svg{transform:rotateZ(90deg)
}
.y-pagination li.next svg{transform:rotateZ(-90deg)
}
.y-pagination li.dd{transition:none;border:none
}
.y-pagination li.dd svg path{fill:#d9d9d9
}
.y-pagination li.dd .next{transform:rotateZ(-180deg)
}
.y-pagination li.dd .prev{transform:rotateZ(0deg)
}
.y-pagination li.page{display:-ms-flexbox;display:flex;algin-item:center;-ms-flex-pack:center;justify-content:center
}
.y-pagination li.active{background:#108ee9;border-color:#108ee9;color:#fff
}
.y-pagination .y-pagination-options{margin-left:16px
}
.y-pagination .y-pagination-options .y-pagination-jumper{height:28px;line-height:28px;vertical-align:middle
}
.y-pagination .y-pagination-options .y-pagination-jumper input{border:1px solid #d9d9d9;height:28px;border-radius:4px;width:40px;outline:none;font-size:14px;padding:4px 6px
}
.y-pagination.simple .y-pagination-slash{margin:0 8px
}
.y-pagination.simple .y-pagination-options{margin:0
}
.y-pagination.simple .y-pagination-options .y-pagination-jumper input{padding:5px 8px;width:26px
}
.y-pagination.simple .page{border:none
}
.y-pagination.simple .next{margin:0;margin-left:8px
}


