
.y-alert{position:relative;padding:8px 8px 8px 16px;border-radius:6px;color:#666;font-size:12px;line-height:16px;margin-bottom:10px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.y-alert>svg{margin-right:6px
}
.y-alert>.y-alert-c{padding-right:40px
}
.y-alert>.y-alert-c .y-title{color:#404040;margin-bottom:4px;font-size:14px;font-weight:700
}
.y-alert>.y-alert-c .y-content{font-size:14px
}
.y-alert>.y-alert-c span{color:#808080
}
.y-alert .y-alert-close{position:absolute;top:50%;right:0;margin-right:12px;margin-top:-7px;font-size:14px;line-height:20px;cursor:pointer
}
.y-alert-info{background:#edf8fd;border:1px #bfe5f8 solid
}
.y-alert-info svg path{fill:#51b7ec
}
.y-alert-info .y-alert-close{color:#51b7ec
}
.y-alert-success{background:#effbf7;border:1px #c7efe4 solid
}
.y-alert-success svg path{fill:#67D5B5
}
.y-alert-success .y-alert-close{color:#67D5B5
}
.y-alert-warning{background:#fff9eb;border:1px #ffe9b8 solid
}
.y-alert-warning svg path{fill:#ffc952
}
.y-alert-warning .y-alert-close{color:#ffc952
}
.y-alert-danger{background:#fff5f0;border:1px #ffd3bd solid
}
.y-alert-danger svg path{fill:#f50
}
.y-alert-danger .y-alert-close{color:#f50
}
.alert-up-enter-active,.alert-up-leave-active{transition:transform .2s,opacity .2s,padding .2s,height .2s,margin .2s;will-change:opacity
}
.alert-up-enter-active,.alert-up-leave-active{opacity:0;margin:0;padding-top:0;padding-bottom:0;transform-origin:0 0;transform:scale3d(1, 0, 1)
}


