
.notification-box{position:fixed;right:20px;top:20px;z-index:999
}
.notification-box .notification{position:relative;width:260px;border:1px #ebebeb solid;background:#fff;border-radius:4px;box-shadow:0 0 40px -5px #ccc;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:12px;margin-bottom:20px;transition:all .5s cubic-bezier(.55, 0, .1, 1)
}
.notification-box .notification .notification-c{margin-left:22px
}
.notification-box .notification .notification-c *{margin:8px 0
}
.notification-box .notification .notification-c h2{font-size:20px
}
.notification-box .notification .notification-c p{color:#808080
}
.notification-box .notification .notification-del{position:absolute;right:16px;top:14px;cursor:pointer;font-size:18px;color:#808080
}
.notification-box .notification .notification-info{font-size:52px;margin-left:12px
}
.notification-box .notification.sm{padding:4px;border:none
}
.notification-box .notification.sm .notification-del{top:50%;margin-top:-12px
}
.notification-box .notification.sm *{color:#fff !important
}
.notification-box .notification.sm.success{background:rgba(103,213,181,0.9)
}
.notification-box .notification.sm.success path{fill:#238065
}
.notification-box .notification.sm.info{background:rgba(81,183,236,0.9)
}
.notification-box .notification.sm.info path{fill:#106794
}
.notification-box .notification.sm.warning{background:rgba(255,201,82,0.9)
}
.notification-box .notification.sm.warning path{fill:#b87f00
}
.notification-box .notification.sm.danger{background:rgba(255,85,0,0.9)
}
.notification-box .notification.sm.danger path{fill:#620
}
.notification-box .notification.success path{fill:#67D5B5
}
.notification-box .notification.success.lg:before{background:#67D5B5
}
.notification-box .notification.info path{fill:#108ee9
}
.notification-box .notification.info.lg:before{background:#108ee9
}
.notification-box .notification.warning path{fill:#ffc952
}
.notification-box .notification.warning.lg:before{background:#ffc952
}
.notification-box .notification.danger path{fill:#ff7473
}
.notification-box .notification.danger.lg:before{background:#ff7473
}
.notification-box .notification.lg:before{content:"";display:block;height:100%;width:3px;background:#ccc;position:absolute;left:0;top:0;border-top-left-radius:4px;border-bottom-left-radius:4px
}
.notification-list{transition:all .5s cubic-bezier(.55, 0, .1, 1)
}
.notification-enter,.notification-active{will-change:opacity
}
.notification-list-enter,.notification-list-leave-active{transform:translateX(100%);opacity:0
}
.notification-list-move{transition:transform 1s
}
.notification-list-leave-active{position:absolute
}


