
.y-modal-box{overflow:auto;background:rgba(255,255,255,0.8);position:fixed;top:0;left:0;bottom:0;right:0;width:100%;height:100%;z-index:991;text-align:center
}
.y-modal-box .y-modal{position:relative;text-align:left;display:inline-block;vertical-align:middle
}
.y-modal-box.y-modal-align-center:before{content:'';display:inline-block;height:100%;vertical-align:middle;width:0
}
.y-modal-box.y-modal-align-center .y-modal{top:0 !important
}
.y-modal-box.y-modal-confirm .y-modal-content,.y-modal-box.y-modal-alert .y-modal-content{padding:24px 42px
}
.y-modal-box.y-modal-confirm .y-modal-content .m-body,.y-modal-box.y-modal-alert .y-modal-content .m-body{padding-left:40px
}
.y-modal-box .y-modal-info path{fill:#108ee9 !important
}
.y-modal-box .y-modal-checkmark path{fill:#67D5B5 !important
}
.y-modal-box .y-modal-close path{fill:#ff7473 !important
}
.y-modal-box .y-modal-help path{fill:#ffc952 !important
}
.y-modal-content{top:0;margin:0 auto;transition:all .3s ease;padding:22px;width:400px;border:1px #ebebeb solid;background:#fff;border-radius:4px;box-shadow:0 0 40px -5px #ccc;box-sizing:border-box;display:inline-block
}
@media (max-width:768px){
.y-modal-content{width:100%;margin:0 12px
}
}
.y-modal-content .m-header h1,.y-modal-content .m-header h2,.y-modal-content .m-header h3,.y-modal-content .m-header h4,.y-modal-content .m-header h5{vertical-align:middle;display:-ms-flexbox;display:flex;height:22px;-ms-flex-align:center;align-items:center;font-size:18px;font-weight:700;color:#566270;margin:0
}
.y-modal-content .m-header h1 svg,.y-modal-content .m-header h2 svg,.y-modal-content .m-header h3 svg,.y-modal-content .m-header h4 svg,.y-modal-content .m-header h5 svg{margin-right:12px
}
.y-modal-content .m-header h1 path,.y-modal-content .m-header h2 path,.y-modal-content .m-header h3 path,.y-modal-content .m-header h4 path,.y-modal-content .m-header h5 path{fill:#fa0
}
.y-modal-content .m-header .notes{font-size:12px;color:#ababab
}
.y-modal-content .m-body{margin-top:16px;margin-bottom:26px;font-size:16px
}
.y-modal-content .m-body *{color:#898989
}
.y-modal-content .m-footer{display:block;text-align:right
}
.y-modal-content .m-footer .y-btn{margin-left:12px
}
.mdc-enter-active,.mdc-leave-active{transition:all .3s ease;will-change:opacity
}
.mdc-enter-active .y-modal,.mdc-leave-active .y-modal{transition:all .3s ease;will-change:transform
}
.mdc-enter{opacity:0
}
.mdc-enter .y-modal{transform:scale(1.1)
}
.mdc-leave-active{opacity:0
}
.mdc-leave-active .y-modal{transform:scale(.9)
}


