
.y-collapse{background-color:#f7f7f7;border-radius:3px;border:1px solid #d9d9d9;overflow:hidden
}
.y-collapse .y-collapse-item{border-top:1px solid #d9d9d9
}
.y-collapse .y-collapse-item svg.arrow{transition:transform .2s cubic-bezier(.215, .61, .355, 1)
}
.y-collapse .y-collapse-item.y-collapse-open svg.arrow{transform:rotate(0deg)
}
.y-collapse .y-collapse-item.y-collapse-close svg.arrow{transform:rotate(-90deg)
}
.y-collapse .y-collapse-item:first-child{border-top:0
}
.y-collapse .y-collapse-item .y-collapse-header{padding:0 16px;height:38px;line-height:38px;vertical-align:middle;cursor:pointer
}
.y-collapse .y-collapse-item .y-collapse-header svg{margin-right:12px
}
.y-collapse .y-collapse-item .y-collapse-content{padding:0 16px;background:#fff;overflow:hidden
}
.y-collapse .y-collapse-item .y-collapse-content .y-collapse-content-box{padding:16px 0
}
.y-collapse .y-collapse-item .y-collapse-content-enter-active,.y-collapse .y-collapse-item .y-collapse-content-leave-active{transition:height .2s cubic-bezier(.215, .61, .355, 1)
}


